<template>
  <div id="home" class="home">
    <div class="container">
      <div class="row">
        <div class="homeMain">
          <div class="homeBuilding">
            <img src="../assets/building.png" alt="">
          </div>
          <p class="homeContext">
            Millions of futures are being built every day, and they come from individuals like you. Through the power of
            open thinking, an open internet, and open source, now is a moment of unrivaled potential. Gone are the days
            of waiting for someone else to find the way. It’s people like you—with an idea and a passion to ignite
            change—that are actively building tomorrow.
          </p>
          <h4 class="homeTitle">What future will you build?</h4>
          <div class="homeFilms">
            <video width="1050" height="590" src="../assets/conrad-operation-code.mp4" autoplay="autoplay" loop="loop">
              这里是注释文字，如果无法支持 HTML 5 浏览器将显示这里的文字。如果支持，就直接显示视频，忽略文字。
            </video>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Home"
  }
</script>

<style scoped>
  .home {
    background: #10181e;
  }

  .homeMain {
    height: 1300px;
  }

  .homeBuilding {
    padding-top: 150px;
    text-align: center;
  }

  .homeContext {
    color: #fff;
    padding: 0 280px;
    text-align: center;
  }

  .homeTitle {
    text-align: center;
    padding-top: 20px;
    color: #fff;
  }

  .homeFilms{
    text-align: center;
    padding-top: 20px;
  }
</style>



